#text-container {
    position: absolute;
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 5;

    width: 70vw;

    text-align: center;
    display: flex;
    flex-direction: column;
}

#header {
    font-size: 60px;
}

#subheader {
    font-size: 30px;
}

Canvas {
    height: 100vh;
    width: 100vw;
}

#gradient {
    background: linear-gradient(white 30%, rgba(0, 0, 0, 0));

    height: 30vh;
    margin-top: -30vh;
    margin-bottom: 30vh;
    transform: rotate(180deg);

    z-index: 5;
}